import 'primeicons/primeicons.css';
import 'primereact/resources/themes/lara-light-indigo/theme.css';
import 'primereact/resources/primereact.css';
import 'primeflex/primeflex.css';
//import '../../index.css';
//import ReactDOM from 'react-dom';

import React, { useEffect, useState } from 'react';
import { useFormik } from 'formik';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
//import { Dropdown } from 'primereact/dropdown';
import { Calendar } from 'primereact/calendar';
import { classNames } from 'primereact/utils';
//import { CountryService } from '../service/CountryService';
//import { CountryService } from './CountryService.jsx';
import Connect from '../api/api.jsx';
//import '../../FormDemo.css';

import { Card } from 'primereact/card';

export const FormikForm = () => {
 //   const [countries, setCountries] = useState([]);
 //   const [showMessage, setShowMessage] = useState(false);
    const [formData, setFormData] = useState({});
  //  const countryservice = new countryservice();
   
    let token= Connect;

    var myHeaders = new Headers();
        myHeaders.append("Authorization", "Bearer");

        var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
        };
        
        
        
       console.log(token);
       console.log(requestOptions);

   /* useEffect(() => {
        countryservice.getCountries().then(data => setCountries(data));
    }, []); */// eslint-disable-line react-hooks/exhaustive-deps
    // console.log(countries);
    const formik = useFormik({
        initialValues: {
            origen: '',
            destino: '',
            ida: '',
            regreso: '',
            adultos: '',
            ninos: ''
        },
        validate: (data) => {
            let errors = {};

            if (!data.origen) {
                errors.origen = 'Origen  es requerido';
            }
            if (!data.destino) {
                errors.destino = 'Destino  es requerido';
            }
            if (!data.ida) {
                errors.ida = 'Fecha de Ida  es requerido';
            }

            if (!data.regreso) {
                errors.regreso = 'Fecha de regreso  es requerido';
            }
            if (!data.adultos) {
                errors.adultos ='Cantidad de persona adulta es requerido';
            }
            return errors;
        },
        

       /*const response= await fetch("https://test.api.amadeus.com/v2/shopping/flight-offers?originLocationCode=MEX&destinationLocationCode=BKK&departureDate=2022-11-01&adults=1", requestOptions)
       let result =await response.json();
        .then(response => response.text())
        .then(result => console.log(result))
        .catch(error => console.log('error', error));*/

        onSubmit: (data) => {
            
          //  console.log(data);
            
            setFormData(data);
            console.log(formData);
            
          //  setShowMessage(true);

            formik.resetForm();
        }
    });

    const isFormFieldValid = (origen) => !!(formik.touched[origen] && formik.errors[origen]);
    const getFormErrorMessage = (origen) => {
        return isFormFieldValid(origen) && <small className="p-error">{formik.errors[origen]}</small>;
    };







    
    return (
        <Card style={{ width: '40em' }} >
        <div className="form-demo">
           

            <div className="flex justify-content-center">
                <div className="card">
                    <h5 className="text-center">---BUSCAR VUELOS---</h5>
              
                    <form onSubmit={formik.handleSubmit} className="p-fluid">
                    <div class="formgrid grid" >
                        <div className="field col-9 md:col-6">
                            <span className="p-float-label">
                                <InputText id="origen" name="origen" value={formik.values.origen} onChange={formik.handleChange} autoFocus className={classNames({ 'p-invalid': isFormFieldValid('origen') })} />
                                <label htmlFor="origen" className={classNames({ 'p-error': isFormFieldValid('origen') })}>Origen</label>
                            </span>
                            {getFormErrorMessage('origen')}
                          
                   
                    </div>
                  
                    <div className="field col-9 md:col-6">
                    <span className="p-float-label">
                                <InputText id="destino" name="destino" value={formik.values.destino} onChange={formik.handleChange} autoFocus className={classNames({ 'p-invalid': isFormFieldValid('destino') })} />
                                <label htmlFor="destino" className={classNames({ 'p-error': isFormFieldValid('destino') })}>Destino</label>
                            </span>
                            {getFormErrorMessage('destino')}
                    </div>
                    <br></br>
                    <div className="field  col-12 md:col-6">
                    <span className="p-float-label">
                    <Calendar id="ida" name="ida" value={formik.values.ida} onChange={formik.handleChange} dateFormat="dd/mm/yy" mask="99/99/9999" showIcon />
                   <label htmlFor="ida">Fecha de  salida</label>
                    </span>
                    {getFormErrorMessage('ida')}
                    </div>
                    <br></br>
                    <div className="field  col-12 md:col-6">
                    <span className="p-float-label">
                    <Calendar id="regreso" name="regreso" value={formik.values.regreso} onChange={formik.handleChange} dateFormat="dd/mm/yy" mask="99/99/9999" showIcon />
                   <label htmlFor="regreso">Fecha de retorno</label>
                    </span>
                    {getFormErrorMessage('regreso')}
                    </div>
                    <br></br>
                    <div className="field col-9 md:col-6">
                    <span className="p-float-label">
                                <InputText id="adultos" name="adultos" value={formik.values.adultos} onChange={formik.handleChange} autoFocus className={classNames({ 'p-invalid': isFormFieldValid('ninos') })} />
                                <label htmlFor="adultos" className={classNames({ 'p-error': isFormFieldValid('adultos') })}>Adultos</label>
                            </span>
                            {getFormErrorMessage('adultos')}
                    </div>
                    <br></br>
                    <div className="field col-9 md:col-6">
                    <span className="p-float-label">
                                <InputText id="ninos" name="ninos" value={formik.values.ninos} onChange={formik.handleChange} autoFocus className={classNames({ 'p-invalid': isFormFieldValid('ninos') })} />
                                <label htmlFor="" className={classNames({ 'p-error': isFormFieldValid('adultos') })}> Nios</label>
                            </span>
                           
                    </div>
                    <br></br>

                        <Button type="submit" label="Buscar Disponibilidad" className="mt-2" />
                   </div>
                   <Connect/>
                    </form>
                   
                </div>
            </div>
        </div>
        </Card>
    );
}
             